import React, { useState, useEffect, lazy, Suspense } from "react";
import { View, ActivityIndicator } from "react-native";
import { MaterialCommunityIcons } from "@expo/vector-icons";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import * as SecureStore from "expo-secure-store";
import {
  useFonts,
  Poppins_800ExtraBold,
  Poppins_100Thin,
  Poppins_500Medium,
  Poppins_400Regular,
  Poppins_300Light,
} from "@expo-google-fonts/poppins";
import AppLoading from "expo-app-loading";
import colors from "./config/colors";

// Lazy-load large screens
const Loginstudent = lazy(() => import("./src/screens/Loginstudent"));
const Loginteacher = lazy(() => import("./src/screens/Loginteacher"));
const Welcome = lazy(() => import("./src/screens/Welcome"));
const Teacherhome = lazy(() => import("./src/screens/Teacherhome"));
const Takeattendance = lazy(() => import("./src/screens/TakeAttendance"));
const Studenthome = lazy(() => import("./src/screens/Studenthome"));
const CreateNotice = lazy(() => import("./src/screens/CreateNotice"));
const PreviousNotices = lazy(() => import("./src/screens/PreviousNotices"));
const LeaveAppeals = lazy(() => import("./src/screens/BrowseLeaveAppeals"));
const AppealLeave = lazy(() => import("./src/screens/AppealLeave"));
const AttendanceRecord = lazy(() => import("./src/screens/AttendanceRecord"));
const ProgressChart = lazy(() => import("./src/screens/progressChart"));
const Rewards = lazy(() => import("./src/screens/rewards"));
const Calendar = lazy(() => import("./src/screens/Calendar"));
const Attendanceinfo = lazy(() => import("./src/screens/Attendanceinfo"));

const Stack = createNativeStackNavigator();

export default function App() {
  const [date, setDate] = useState(null);
  const [defaultScreen, setDefaultScreen] = useState("Welcome");

  const getToken = async () => {
    const token = await SecureStore.getItemAsync("user");
    if (token) {
      console.log(token);
      const role = JSON.parse(token).role;
      setDefaultScreen(role === "student" ? "Calendar" : "Teacherhome");
    }
  };

  useEffect(() => {
    getToken();
    const today = new Date();
    setDate(`${today.getFullYear()}-${today.getMonth() + 1}-${today.getDate()}`);
  }, []);

  let [fontsLoaded] = useFonts({
    Poppins_800ExtraBold,
    Poppins_500Medium,
    Poppins_100Thin,
    Poppins_400Regular,
    Poppins_300Light,
  });

  if (!fontsLoaded) {
    return <AppLoading />;
  }

  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName={defaultScreen}>
        <Stack.Screen
          name="Attendance"
          component={Takeattendance}
          options={{
            title: `Attendance: ${date}`,
            headerStyle: { backgroundColor: colors.primary },
            headerTintColor: colors.white,
          }}
        />
        {/* Suspense for lazy-loaded screens */}
        <Suspense fallback={<ActivityIndicator size="large" color={colors.primary} />}>
          <Stack.Screen name="Welcome" component={Welcome} options={{ headerShown: false }} />
          <Stack.Screen name="Loginp" component={Loginstudent} options={{ headerShown: false }} />
          <Stack.Screen name="Logint" component={Loginteacher} options={{ headerShown: false }} />
          <Stack.Screen name="Teacherhome" component={Teacherhome} options={{ headerShown: false }} />
          <Stack.Screen name="progressChart" component={ProgressChart} options={{ headerShown: false }} />
          <Stack.Screen name="rewards" component={Rewards} options={{ headerShown: false }} />
          <Stack.Screen name="Studenthome" component={Studenthome} options={{ headerShown: false }} />
          <Stack.Screen
            name="Calendar"
            component={Calendar}
            options={{
              title: "Attendance Calendar",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="Attendancerecord"
            component={AttendanceRecord}
            options={{
              title: "Attendance record",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="CreateNotice"
            component={CreateNotice}
            options={{
              title: "Create Notice",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="PreviousNotices"
            component={PreviousNotices}
            options={{
              title: "Previous Notices",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="BrowseLeaveAppeals"
            component={LeaveAppeals}
            options={{
              title: "Leave Appeals",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="AppealLeave"
            component={AppealLeave}
            options={{
              title: "Appeal leave",
              headerStyle: { backgroundColor: colors.primary },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="Studentinfo"
            component={Attendanceinfo}
            options={{
              title: "",
              headerTransparent: true,
              headerTintColor: colors.white,
            }}
          />
        </Suspense>
      </Stack.Navigator>
    </NavigationContainer>
  );
}
